.cards__body {
  display: flex;
  flex-direction: column;
  gap: 80px;
}
@media (max-width: 1025px) {
  .cards__body {
    gap: 90px;
  }
}
@media (max-width: 768px) {
  .cards__body {
    gap: 50px;
  }
}

@media (max-width: 768px) {
  .cards__bod--ds {
    display: none;
  }
}

.cards__bod--mob {
  display: none;
}
@media (max-width: 768px) {
  .cards__bod--mob {
    display: flex;
  }
}

.card__sports,
.card__corporiate,
.card__sidebar {
  position: relative;
}

.card__sports img,
.card__corporiate img,
.card__sidebar img {
  width: 100%;
  max-height: 750px;
  object-fit: cover;
  border-radius: 8px;
}
@media (max-width: 1225px) {
  .card__sports img,
  .card__corporiate img,
  .card__sidebar img {
    max-height: 600px;
  }
}
@media (max-width: 768px) {
  .card__sports img,
  .card__corporiate img,
  .card__sidebar img {
    max-height: 260px;
  }
}

.card__footer {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-inline: 40px;
  bottom: 40px;
  gap: 15px;
  align-items: center;
}
@media (max-width: 1025px) {
  .card__footer {
    padding-inline: 30px;
    bottom: 30px;
  }
}
@media (max-width: 768px) {
  .card__footer {
    padding-inline-start: 0px;
    padding-inline-end: 15px;
    bottom: 0;
    left: 0;
  }
}

.card__footer-link {
  padding: 15px;
  background-color: #222233;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-block-start: 20px;
}
@media (max-width: 768px) {
  .card__footer-link {
    margin-block-start: -4px;
  }
}

.card__footer-link img {
  min-width: 15px;
  height: 15px;
}

.card__footer-text p {
  padding: 20px;
  background-color: #222233;
  border-radius: 8px;
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
}
@media (max-width: 1025px) {
  .card__footer-text p {
    letter-spacing: 0.1px;
  }
}
@media (max-width: 768px) {
  .card__footer-text p {
    padding-block: 10px;
    padding-inline: 15px;
  }
}

.card__footer-link {
  width: 44px;
  height: 44px;
  cursor: pointer;
}

.card__footer-link--green {
  background-color: #57efb4;
}